import React, { useState } from 'react';
import ReactMarkdown from 'react-markdown';
import Checklist from './Checklist';

function Areas() {
  const [taskCounts, setTaskCounts] = useState({ area1: 0, area2: 0, area3: 0 });

  const tasksForArea1 = [
    { id: 1, name: 'Proceed through the clinic. If this is your first time playing, read the messages on the ground for information about the controls', completed: false },
    { id: 2, name: 'Let yourself be killed by the werewolf and awaken in the [Hunter\'s Dream](https://bloodborne.fandom.com/wiki/Hunter%27s_Dream)', completed: false },
    // Add more tasks as needed
  ];

  const tasksForArea2 = [
    { id: 3, name: 'Task 3', completed: false },
    { id: 4, name: 'Task 4', completed: false },
    // Add more tasks as needed
  ];

  const tasksForArea3 = [
    { id: 5, name: 'Task 5', completed: false },
    { id: 6, name: 'Task 6', completed: false },
    // Add more tasks as needed
  ];

  const handleTaskListChange = (area, taskList) => {
    setTaskCounts({ ...taskCounts, [area]: taskList.filter(task => task.completed).length });
  };

  return (
    <div className="content">
      <h2><a name="area1">Iosefka's Clinic ({taskCounts.area1})</a></h2>
      <Checklist tasks={tasksForArea1} onTaskListChange={(taskList) => handleTaskListChange('area1', taskList)} />

      <h2><a name="area2">Area 2 ({taskCounts.area2})</a></h2>
      <Checklist tasks={tasksForArea2} onTaskListChange={(taskList) => handleTaskListChange('area2', taskList)} />
      <p>Content for Area 2</p>

      <h2><a name="area3">Area 3 ({taskCounts.area3})</a></h2>
      <Checklist tasks={tasksForArea3} onTaskListChange={(taskList) => handleTaskListChange('area3', taskList)} />
      <p>Content for Area 3</p>
    </div>
  );
}

export default Areas;