import React, { useState, useEffect } from 'react';
import ReactMarkdown from 'react-markdown';
import { Link } from 'react-router-dom';

function Checklist({ tasks = [], onTaskListChange = () => {} }) {
  const [taskList, setTaskList] = useState(tasks);

  useEffect(() => {
    onTaskListChange(taskList);
  }, [taskList, onTaskListChange]);

  const handleTaskClick = (taskId) => {
    setTaskList(taskList.map(task => 
      task.id === taskId ? { ...task, completed: !task.completed } : task
    ));
  };

  return (
    <div className="checklist">
      <ul style={{ margin: 0, padding: 0 }}>
        {taskList.map(task => (
          <li key={task.id} style={{ display: 'flex', alignItems: 'center', margin: 0, padding: 0 }}>
            <label style={{ display: 'flex', alignItems: 'center', width: '100%' }}>
              <input type="checkbox" checked={task.completed} onChange={() => handleTaskClick(task.id)} />
              <span style={{ textDecoration: task.completed ? 'line-through' : 'none' }}>
                <ReactMarkdown
                  components={{
                    // This custom component changes all links to open in a new tab
                    a: ({node, ...props}) => <a {...props} href={props.href} target="_blank" rel="noopener noreferrer" />
                  }}
                >
                  {task.name}
                </ReactMarkdown>
              </span>
            </label>
          </li>
        ))}
      </ul>
    </div>
  );
}

export default Checklist;